export default {
    props: {
        placeholder: {
            type: String,
            default: '搜索店铺'
        },
        bgStyle: {
            type: String,
            default: ''
        },
        inputStyle: {
            type: String,
            default: ''
        },
        disabled: {
            type: Boolean,
            default: false
        },
        value:{
            type:String,
            default:''
        }
    },
    watch:{
        value(){
            this.val=this.value;
        }
    },
    mounted(){
         this.val=this.value;
    },
    data() {
        return {
            val: ''
        }
    },
    computed: {
        showClear() {
            return this.val !== '';
        }
    },
    methods: {
        getInput(val) {
            if (val == 'clear') {
                this.val = '';
                this.$emit('clear')
            } else {
                this.val = val.detail.value;
                this.$emit(val.type, val.detail)
            }
        },
        clickSearch() {
            this.$emit('click', this.val)
        },
        click() {
            this.$emit('search', this.val)
        }
    },
}
